<template>
  <div class="row">
    <div class="col-12 mt-3" v-if="isReady && store">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">Membership plan</h5>
          </div>
        </div>
        <slot name="noti"></slot>
        <div class="col-lg-4 mb-3 mt-4">
          <p class="font-weight-bold">Plan details</p>
          <p class="gray mt-3 normal-text">
            Manage your subscription and view the details of your plan.
          </p>
        </div>
        <div class="col-lg-8 mb-3 pl-lg-0">
          <div class="box">
            <p class="mt-0 mb-2 text-bold">Membership plan</p>
            <p class="">Current plan: {{ store.plan.plan.name }}</p>
            <div class="b-divider"></div>
            <p class="mt-0 mb-2 text-bold">NEXT BILLING DATE</p>
            <p class="">
              Expired date:
              {{ $moment(store.plan.exp_date).format("DD-MM-YYYY") }}
            </p>
            <p class="gray normal-text" v-if="!isStoreExpired">
              Your plan has
              {{ $moment(store.plan.exp_date).diff($moment(), "days") }} days
              remaining
            </p>
            <p class="danger normal-text" v-else>Your plan has been expired!</p>
            <div class="b-divider"></div>
            <div class="flex-end">
              <el-button
                class="ml-2 flat"
                type="primary mb-0"
                v-ripple.400
                @click="
                  $router.push({
                    name: 'Billing History',
                  })
                "
                >Billing history</el-button
              >
              <el-button
                class="ml-2"
                type="primary mb-0"
                v-ripple.400
                @click="
                  $router.push({
                    name: 'Add Billing',
                    query: {
                      pid: store.plan.plan.id,
                    },
                  })
                "
                >Update plan</el-button
              >
              <!-- <el-button
                class="ml-2 mb-0"
                type="primary"
                v-ripple.400
                @click="$router.push({ name: 'Plans' })"
                >Choose plan</el-button
              > -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  watch: {},
  mounted() {
    this.$store.commit("setIsReady", true);
  },
  methods: {},
  computed: {
    ...mapGetters(["isStoreExpired"]),
    ...mapState(["store", "isReady"]),
  },
};
</script>

